import VueRouter from 'vue-router'
import Vue from 'vue'

import Login from '../pages/Login'
Vue.use(VueRouter)

import { checktoken } from '../API/apis'
// import { metadata } from 'core-js/fn/reflect'

// 路由拦截
let router = new VueRouter({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login//登录
    },
    {
      path: '/index',
      name: 'index',
      component: () => import('../pages/Index'),//main
      // 二级
      children: [
        {
          path: '/index/Home',
          name: '/index/Home',
          component: () => import('../pages/mains/Home'),//后台首页
          meta: { breadlist: ["首页"] }
        },
        {
          path: '/index/Order',
          name: '/index/Order',
          component: () => import('../pages/mains/Order'),//订单管理
          meta: { breadlist: ["订单管理"] }
        },
        {
          path: '/index/Orderpage',
          name: '/index/Orderpage',
          component: () => import('../pages/mains/Orderpage'),//商品列表
          meta: { breadlist: ["商品管理", "商品列表"] }
        },
        {
          path: '/index/Orderadd',
          name: '/index/Orderadd',
          component: () => import('../pages/mains/Orderadd'),//商品添加
          meta: { breadlist: ["商品管理", "商品添加"] }
        },
        {
          path: '/index/Ordercategory',
          name: '/index/Ordercategory',
          component: () => import('../pages/mains/Ordercategory'),//商品分类
          meta: { breadlist: ["商品管理", "商品分类"] }
        },
        {
          path: '/index/Store',
          name: '/index/Store',
          component: () => import('../pages/mains/Store'),//店铺管理
          meta: { breadlist: ["店铺管理"] }
        },
        // 账号管理
        {
          path: '/index/Accountlist',
          name: '/index/Accountlist',
          component: () => import('../pages/Account/Accountlist'),//账号列表
          meta: { breadlist: ["商品管理", "商品分类"] }
        },

        {
          path: '/index/Accountinfo',
          name: '/index/Accountinfo',
          component: () => import('../pages/Account/Accountinfo'),//个人中心
          meta: { breadlist: ["个人中心"] }
        },
        {
          path: '/index/Accountadd',
          name: '/index/Accountadd',
          component: () => import('../pages/Account/Accountadd'),//添加账号
          meta: { breadlist: ["账号管理", "添加账号"] }
        },
        {
          path: '/index/Changepwd',
          name: '/index/Changepwd',
          component: () => import('../pages/Account/Changepwd'),//修改密码
          meta: { breadlist: ["账号管理", "修改密码"] }
        },
        //销售统计
        {
          path: '/index/Orderinfo',
          name: '/index/Orderinfo',
          component: () => import('../pages/mains/Orderinfo'),//商品统计
          meta: { breadlist: ["销售统计", "商品统计"] }
        },
        {
          path: '/index/Indetinfo',
          name: '/index/Indetinfo',
          component: () => import('../pages/mains/Indetinfo'),//订单统计
          meta: { breadlist: ["销售统计", "订单统计"] }
        },

      ]
    },
  ]
})
router.beforeEach((to, from, next) => {
  if (to.path !== "/") {
    checktoken(localStorage.token).then(res => {
      if (res.data.code == 0)
        next()
      else next({ path: "/" })
    })
  } else next()
})

export default router