import axios from 'axios'
// 服务器IP
const IP = 'http://127.0.0.1:5000'
// 请求基本路径 
axios.defaults.baseURL = IP
//头像等上传图片接口
export var ITEMS_UP_IMG = IP + "/goods/goods_img_upload"
//店铺图片上传接口
export var SHOP_UP_IMG = IP + "/shop/upload"
//获取商品上传的图片
export var GET_IMG = IP + "/upload/imgs/goods_img/"
// 获取商店上传图片
export var GET_IMG_store = IP + "/upload/shop/"
//登录
//account用户名
//password密码
export var login = (account, password) => axios.post('/users/checkLogin', { account, password })
//添加账号
//userGroup：用户组
export var useradd = (account, password, userGroup) => axios.post('/users/add', { account, password, userGroup })
//获取账号列表
//currentPage:当前页码
//pageSize:每页条数
export var userlist = (currentPage, pageSize) => axios.get('/users/list', { params: { currentPage, pageSize } })
//删除账号
export var userdel = (id) => axios.get('/users/del', { params: { id } })
//批量删除账号
//ids:要删除数据的id组成的数组
export var batchdel = (ids) => axios.get('/users/batchdel', { params: { ids } })
//修改账号
export var edit = (id, account, userGroup) => axios.post('/users/edit', { id, account, userGroup })
//检查旧密码是否正确
export var checkoldpwd = (oldPwd, id) => axios.get('/users/checkoldpwd', { params: { oldPwd, id } })
//修改密码
export var editpwd = (newPwd, id) => axios.post('/users/editpwd', { newPwd, id })
//获取账号（个人中心）信息
export var accountinfo = (id) => axios.get('/users/accountinfo', { params: { id } })
//头像上传接口
export var avatar_upload = (id) => axios.post('/users/avatar_upload', { id })
//验证token
export var checktoken = (token) => axios.get('/users/checktoken', { params: { token } })
//-----------------------------------------------------------------------------------------------------------------------------------------------------------//
//添加商品分类
//cateName:分类名称editcate
//state:是否启用
export var addcate = (cateName, state) => axios.post('/goods/addcate', { cateName, state })
//获取商品分类
export var catelist = (currentPage, pageSize) => axios.get('/goods/catelist', { params: { currentPage, pageSize } })
//删除商品分类
export var delcate = (id) => axios.get('/goods/delcate', { params: { id } })
//修改分类
//cateName:分类名称
export var editcate = (id, cateName, state) => axios.post('/goods/editcate', { id, cateName, state })
//查询所有分类名称
export var categories = () => axios.get('/goods/categories', { params: {} })
//商品图片上传接口
export var goods_img_upload = () => axios.post('/goods/goods_img_upload', {})
//添加商品
// name:商品名称
// category:	商品分类
// price:商品价格
// imgUrl:	商品图片地址
// goodsDesc:商品描述
//--------------------------------------------------------------商品---------------------------------------------------------------------------------------------//
export var addorder = (name, category, price, imgUrl, goodsDesc) => axios.post('/goods/add', { name, category, price, imgUrl, goodsDesc })
//获取商品列表
export var list = (currentPage, pageSize) => axios.get('/goods/list', { params: { currentPage, pageSize } })
//删除商品
export var del = (id) => axios.get('/goods/del', { params: { id } })
//修改商品
export var good_edit = (name, category, price, imgUrl, goodsDesc, id) => axios.post('/goods/edit', { name, category, price, imgUrl, goodsDesc, id })
//--------------------------------------------------------------☆订单---------------------------------------------------------------------------------------------//
//获取订单列表(带查询功能)
export var orderlist = (currentPage, pageSize) => axios.get('/order/list', { params: { currentPage, pageSize } })
//订单查询
export var ordersearch = (currentPage, pageSize, orderNo, consignee, phone, orderState, date) => axios.get('/order/list', { params: { currentPage, pageSize, orderNo, consignee, phone, orderState, date } })
// 获取订单详情
export var orderdetail = (id) => axios.get('/order/detail', { params: { id } })
//修改订单
export var orderedit = (id, orderNo, orderTime, phone, consignee, deliverAddress, deliveryTime, remarks, orderAmount, orderState) => axios.post('/order/edit', { id, orderNo, orderTime, phone, consignee, deliverAddress, deliveryTime, remarks, orderAmount, orderState })
//--------------------------------------------------------------店铺---------------------------------------------------------------------------------------------//
//获取店铺详情
export var shopinfo = () => axios.get('/shop/info', { params: {} })
// 店铺内容修改
export var shopedit = (id, name, bulletin, avatar, deliveryPrice, deliveryTime, description, score, sellCount, supports, date, pics) => axios.post('/shop/edit', { id, name, bulletin, avatar, deliveryPrice, deliveryTime, description, score, sellCount, supports, date, pics })
//--------------------------------------------------------------☆报表统计---------------------------------------------------------------------------------------------//
//首页报表接口
export var totaldata = () => axios.get('/order/totaldata', { params: {} })
//订单报表接口
export var ordertotal = (date) => axios.get('/order/ordertotal', { params: { date } })

