<template>
  <div id="Box1_div">
    <span id="logoS"></span>
    <div id="Login_div">
      <h1>老酒管理系统</h1>
      <el-input v-model="acc"
                clearable
                placeholder="请输入用户名">
      </el-input>
      <el-input v-model="pwd"
                show-password
                clearable
                prefix-icon="iconfont icon-sousuo"
                placeholder="请输入密码"
                @keyup.enter.native="clickLogin"></el-input>
      <p class="errColor"
         :style="errColors">账号或密码错误</p>
      <el-button type="primary"
                 style="width:100%"
                 @click="clickLogin">登录</el-button>
    </div>
  </div>
</template>

<script>
//引入
import { login } from '../API/apis'
export default {
  data () {
    return {
      acc: '',
      pwd: '',
      errColors: "color:transparent"
    }
  },
  methods: {
    clickLogin () {
      login(this.acc, this.pwd).then(res => {
        if (res.data.code == 0) {
          //跳转到首页
          this.$router.push("/index/home")
          // 储存用户名到本地
          localStorage.acc = this.acc
          //储存token到本地
          localStorage.token = res.data.token
          //储存用户组
          localStorage.role = res.data.role
          localStorage.id = res.data.id
          this.$message({
            message: res.data.msg,
            type: 'success'
          });
        } else {
          this.errColors = "color:brown"
        }
      })

    }
  }
}

</script>

<style lang="less" scoped>
#Box1_div {
  #logoS {
    display: inline-block;
    width: 100px;
    height: 50%;
    background: url(../assets/imgs/06.png) no-repeat;
  }
  background: url(../assets/imgs/05.png);
  background-position: center right;
  background-position-x: 300px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  #Login_div {
    transition: all 0.5s;
    margin-right: 100px;
    width: 400px;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 30px;
    border-radius: 15px;
    h1 {
      color: white;
      text-align: center;
      margin-bottom: 5px;
      font-family: "微软雅黑";
    }
    .el-input {
      margin-top: 25px;
      border-radius: 3px;
    }

    button {
      background-color: #666666;
      border: 1px solid transparent;
    }
  }
  #Login_div:hover {
    background-color: #1b1a1a;
  }
  .errColor {
    text-align: center;
    font-size: 14px;
    line-height: 25px;
  }
}
</style>